// 
let ajaxTimes=0;

// url
const baseUrl="http://localhost:80";
/**
 * baseUrl
 */
export const getBaseUrl=()=>{
  return baseUrl;
}

/**
 * 
 * @param {*} params 
 */
export const requestUtil=(params)=>{
   let header={...params.header};
 
   // header token
   header["token"]=uni.getStorageSync("token");

   ajaxTimes++;
	
   //  
   wx.showLoading({
     title: "",
     mask: true
   });

   console.log(':', {
     url: baseUrl + params.url,
     method: params.method,
     data: params.data
   });

   return new Promise((resolve,reject)=>{
     wx.request({
       ...params,
       header:header,
       url:baseUrl+params.url,
       success:(result)=>{
         console.log(':', result);
         if(result.statusCode === 200){
           resolve(result.data);
         } else {
           uni.showToast({
             icon: 'error',
             title: '',
             duration: 2000
           });
           reject(new Error(': ' + result.statusCode));
         }
       },
       fail: function fail(err) {
         console.error(':', err);
         uni.showToast({
           icon: 'error',
           title: err.errMsg || '',
           duration: 2000
         });
         reject(err);
       },
       complete:()=>{
         ajaxTimes--;
         if(ajaxTimes===0){
           //  
           wx.hideLoading();
         }
       }
     });
   })
}